.cardT {
  margin: 20px 30px;
  padding: 0px 0px;
  min-height: 490px;
}
.contentLeft {
  margin: 0px 0px;
  padding: 0px 0px;
  width: 100%;
  height: 490px;
  border-right: 2px solid #e9ecef;
}
.contentRight {
  margin: 0px 0px;
  padding: 20px 20px;
}
.tabsbar {
  clear: both;
  width: 100%;
  height: 100%;
  position: relative;
  float: right;
  padding: 0px 0px;
  border: 0px;
}
.activeT {
  font-weight: bold;
  border-left-style: groove;
  border-left-width: 7px;
}
.avatar {
  margin: auto 20px;
  border-radius: 50%;
}
.buttonOrange2 {
    width: 15%;
    background: #F77F00;
    border-style: none;
}